import useMediaQuery from '@mui/material/useMediaQuery';
import { Link } from 'react-router-dom';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import { Branding } from '../brand';
import { TopBarGridContainer, TopBarButton } from './utils';
import { MiscLinks } from './misclinks';
import BurgerMenu from './BurgerMenu';

function BrandLink() {
  const belowsm = useMediaQuery((theme) => theme.breakpoints.down('sm'));
  const branding = belowsm ? <Branding width="55px" height="55px" fontSize="1.55rem" /> : <Branding />;
  return (
    <Link style={{ textDecoration: 'none' }} to="/memories">
      <Button>
        {branding}
      </Button>
    </Link>
  );
}

function MyMemoriesButton() {
  return (
    <Grid
      item
      sx={{
        paddingTop: '0.55rem',
        position: 'fixed',
        left: '50vw',
        transform: 'translate(-50%)',
      }}
    >
      <Link style={{ textDecoration: 'none' }} to="/memories">
        <TopBarButton text="My Memories" borderColor="secondary.main" onClick={() => {}} />
      </Link>
    </Grid>
  );
}

export default function TopBar() {
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));
  const belowsm = useMediaQuery((theme) => theme.breakpoints.down('sm'));

  if (belowsm) {
    return (
      <TopBarGridContainer>
        <BrandLink />
      </TopBarGridContainer>
    );
  }

  if (belowmd) {
    return (
      <TopBarGridContainer>
        <BurgerMenu />
        <BrandLink />
        <MiscLinks />
      </TopBarGridContainer>
    );
  }

  return (
    <TopBarGridContainer>
      <BrandLink />
      <MyMemoriesButton />
      <MiscLinks />
    </TopBarGridContainer>
  );
}
